import React, {Component} from 'react';
import {Card} from 'react-bootstrap';
import {FormattedMessage} from 'react-intl';
import './NotableProjects.css';

class NotableProjects extends Component{

    card_layout(keys){    
        return (
            <Card className="work-card-content">
                <Card.Body>
                    <Card.Title><FormattedMessage id={keys[0]}/></Card.Title>
                    <Card.Subtitle><FormattedMessage id={keys[1]}/></Card.Subtitle>
                    <Card.Subtitle className="mt-2"><FormattedMessage id={keys[2]}/></Card.Subtitle>
                    <Card.Text>
                    Tasks
                    <ul>                        
                        <li><FormattedMessage id={keys[3]}/></li>
                    </ul>
                    </Card.Text>
                </Card.Body>
            </Card> 
        );
    }

    render(){
        return(
            <div>
                <p className="Notable-title"><FormattedMessage id="lbl_notableProjects"/></p>
            </div>
        );
    }
}
export default NotableProjects;