import {addLocaleData, IntlProvider} from 'react-intl';
import locale_en from 'react-intl/locale-data/en';
import locale_el from 'react-intl/locale-data/el';
import content_en from './translations/en';
import content_el from './translations/el';
import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import './bootstrap/css/bootstrap.min.css';
import App from './App';
import * as serviceWorker from './serviceWorker';

addLocaleData(locale_en,locale_el);

const content = {
    'en' : content_en,
    'el' : content_el
}

const sys_lang = navigator.language.split(/[-_]/)[0];

ReactDOM.render(
        <IntlProvider locale={sys_lang} messages={content[sys_lang]}>
            <App/>
        </IntlProvider>, 
        document.getElementById('root')
    );

// If you want your app to work offline and load faster, you can change
// unregister() to register() below. Note this comes with some pitfalls.
// Learn more about service workers: http://bit.ly/CRA-PWA
serviceWorker.unregister();
