import {FormattedMessage} from 'react-intl';
import React, {Component} from 'react';
import { Row, Col, Card, Image, Button } from 'react-bootstrap';
import './Information.css';
import image from '../res/image.jpg';

class Information extends Component{
    render(){
        return(
            <div>
                <div className="mt-5 text-center">
                    <Image src={image} width="140" height="140" thumbnail />
                </div>

                <p className="text-center personal-name"><FormattedMessage id="name"/></p>
                <p className="text-center personal-subtitle"><FormattedMessage id="title"/></p>
                <h1 className="personal-title-info text-center mt-1 mb-1"><FormattedMessage id="lbl_info"/></h1>
                
                <Card className="mr-5 mt-4 ml-5 personal-card-content">
                    <div className="text-center">
                        <p className="mt-4 mb-2 text-center"><FormattedMessage id="bio"/></p>
                    </div>
                    <hr></hr>
                    <Row>
                        <Col>
                            <ul className="mt-3 mb-3 text-right">
                                <li><p><FormattedMessage id="phone"/></p></li>
                                <li><p><FormattedMessage id="address"/></p></li>
                                <li><p>Address</p></li>
                            </ul>
                        </Col>
                        <Col>
                            <ul className="mt-3 mb-3 text-left">
                                <li><p><FormattedMessage id="mail"/></p></li>
                                <li><p><FormattedMessage id="status"/></p></li>
                                <li><p><FormattedMessage id="languages"/></p></li>
                            </ul>
                        </Col>
                    </Row>
                    
                </Card> 
                               
                
                <Row className="mt-5 mb-5">
                    <Col className="text-center">
                        <Button variant="info" className="ml-2 mr-2">Linked in</Button>
                        <Button variant="info" className="mr-2">github</Button>
                        <Button variant="info" className="mr-2">Mail</Button>
                    </Col>
                </Row>
                
                
            </div>
        );
        
    }
}

export default Information;