import {FormattedMessage} from 'react-intl';
import React, {Component} from 'react';
import {Card, Col, Row, Button} from 'react-bootstrap';
import './Work.css';

class Work extends Component{

    // Basic card layout 
    card_layout(item){    
        var e = item[3];
        var li_items = [];
        for (var i in e){
            li_items.push('<li>'+i+'</li>');
        }
        return (
            <Card className="work-card-content">
                <Card.Body>
                    <Card.Title><FormattedMessage id={item[0]}/></Card.Title>
                    <Card.Subtitle><FormattedMessage id={item[1]}/></Card.Subtitle>
                    <Card.Subtitle className="mt-2"><FormattedMessage id={item[2]}/></Card.Subtitle>
                    <Card.Text>
                        <p>Tasks</p>
                        <ul>                        
                            {this.li_items}
                        </ul>
                    </Card.Text>
                </Card.Body>
            </Card> 
        );
    }
    // this will add all cards
    working_experience_builder(){
        // list with working experience 
        var data = [
            ["exp1-position","exp1-company","exp1-period",["exp1-li-msg1","exp1-li-msg2","exp1-li-msg3"]],
            ["exp2-position","exp2-company","exp2-period",["exp2-li-msg1","exp2-li-msg2","exp2-li-msg3"]],
            ["exp3-position","exp3-company","exp3-period",["exp3-li-msg1","exp3-li-msg2"]]
        ];
        var cards = [];
        for (var item in data){
            cards.push(this.card_layout(item));
        }

        return(
            this.cards
        )                    
    }
    

    render(){ 
        return(
            <div>
                <p class="work-title text-center mb-3 mt-4"><FormattedMessage id="lbl_work"/></p>
                
                <Row className="mt-3 ml-4 mr-4">
                    <Col>
                        <Card className="work-card-content">
                            <Card.Body>
                                <Card.Title><FormattedMessage id="exp1-position"/></Card.Title>
                                <Card.Subtitle><FormattedMessage id="exp1-company"/></Card.Subtitle>
                                <Card.Subtitle className="mt-2"><FormattedMessage id="exp1-period"/></Card.Subtitle>
                                <Card.Text>
                                    Tasks
                                    <ul>
                                        <li><FormattedMessage id="exp1-li-msg1"/></li>
                                        <li><FormattedMessage id="exp1-li-msg2"/></li>
                                        <li><FormattedMessage id="exp1-li-msg3"/></li>
                                    </ul>
                                </Card.Text>
                            </Card.Body>
                        </Card>
                    </Col>
                    <Col>
                        <Card className="work-card-content">
                            <Card.Body>
                                <Card.Title><FormattedMessage id="exp2-position"/></Card.Title>
                                <Card.Subtitle><FormattedMessage id="exp2-company"/></Card.Subtitle>
                                <Card.Subtitle className="mt-2"><FormattedMessage id="exp2-period"/></Card.Subtitle>
                                    <Card.Text>
                                        Tasks
                                        <ul>
                                            <li><FormattedMessage id="exp2-li-msg1"/></li>
                                            <li><FormattedMessage id="exp2-li-msg2"/></li>
                                            <li><FormattedMessage id="exp2-li-msg3"/></li>
                                        </ul>
                                    </Card.Text>
                                    <Button variant="outline-primary"><FormattedMessage id="btn-source-code"/></Button>
                            </Card.Body>
                        </Card>
                    </Col>
                    <Row className="ml-3 mt-3 mr-4">
                    <Card className="work-card-content">
                        <Card.Body>
                                <Card.Title><FormattedMessage id="exp3-position"/></Card.Title>
                                <Card.Subtitle><FormattedMessage id="exp3-company"/></Card.Subtitle>
                                <Card.Subtitle className="mt-2"><FormattedMessage id="exp3-period"/></Card.Subtitle>
                                <Card.Text>
                                <p>Tasks</p>
                                    <ul>
                                        <li><FormattedMessage id="exp3-li-msg1"/></li>
                                        <li><FormattedMessage id="exp3-li-msg2"/></li>
                                    </ul>
                                </Card.Text>
                            </Card.Body>
                        </Card>
                    </Row>
                </Row>
                  
            </div>
        );
    }
}
export default Work;