import React, {Component} from 'react';
import {FormattedMessage} from 'react-intl';
import {Card,Row,Col,Button} from 'react-bootstrap';
import './Education.css';
import thesis_logo from '../res/thesis_logo1.png';

class Education extends Component{
    education_info(){
        return(
            <div>
                <p><FormattedMessage id="edu-title"/></p>
                <p><FormattedMessage id="edu-institute"/></p>
                <p><FormattedMessage id="edu-period"/></p>

            </div>
            
          
        );
    }

    thesis_project(){
        return(
        <Card className="work-card-content">
            <Card.Body>
                <Card.Img width="60" height="200" src={thesis_logo}></Card.Img>
                <Card.Title><FormattedMessage id="exp2-position"/></Card.Title>
                <Card.Subtitle><FormattedMessage id="exp2-company"/></Card.Subtitle>
                <Card.Subtitle className="mt-2"><FormattedMessage id="exp2-period"/></Card.Subtitle>
                    <Card.Text>
                        Tasks
                        <ul>
                            <li><FormattedMessage id="exp2-li-msg1"/></li>
                            <li><FormattedMessage id="exp2-li-msg2"/></li>
                            <li><FormattedMessage id="exp2-li-msg3"/></li>
                        </ul>
                    </Card.Text>
                <Button variant="outline-primary"><FormattedMessage id="btn-source-code"/></Button>
            </Card.Body>
        </Card>
        );
    }
    render(){
        return(
            <div>
                <p className="education-title text-center mt-4">Education</p>
                <Row className="mt-3 ml-4 mr-4">
                    <Col>
                        { this.education_info() }
                    </Col>
                    <Col> 
                        { this.thesis_project() }
                    </Col>
                </Row>
            </div>
        );
    }
}
export default Education;